const defaultReferrer = '0x0000000000000000000000000000000000000000';
async function populateReferrerinHTML(userAddress) {
    console.log(`populateReferrerinHTML - ${userAddress}`);
    let referrerValue = userAddress; //which will be 'logged out' if user is logged out
    // if user is logged-in we change the value of refferValue
    if (isUserConnected()) referrerValue = await getReferrer(userAddress);
    // show referrer value only if it's not default : i.e. something other than 0x00...0

    const referrerField = document.querySelector('.refer-referrer');
    // if (referrerValue !== defaultReferrer) {
    console.log(`dev:Setting Referrer Field`);
    if (referrerField) {
        if (referrerValue === defaultReferrer) {
            referrerField.value = 'Referrer Not Added'
        } else referrerField.value = referrerValue;
    } else {
        console.error(`dev:referrerField missing`);
    }
    // const referrerFields = document.querySelectorAll('.refer-referrer');
    // const referrerFieldsContainer = document.querySelectorAll('.refer-referrer-container');
    // referrerFieldsContainer && referrerFieldsContainer.forEach(el => {
    //     el.classList.remove('hidden');
    // });
    // referrerFields && referrerFields.forEach(el => {
    //     // in this example - the userAddress = logged out
    //     el.value = referrerValue;
    // });
    // }

    // uncomment the below line - to check if the addReferrerBtn will show when the reffere is 0x00...00 or not
    // referrerValue = '0x0000000000000000000000000000000000000000'
    /**
     * if referrer is 0x0000000000000000000000000000000000000000
     * then show the ability to change / setReferrer
     * else keep it hidden
     */
    const setRefferrerBtn = document.querySelector('#add-referrer');
    if (setRefferrerBtn) {
        if (referrerValue === defaultReferrer) setRefferrerBtn.classList.remove('hidden')
        else setRefferrerBtn.classList.add('hidden')
    }
    const addReferrerBtn = document.getElementById('add-referrer-btn');
    const addReferrerInput = document.getElementById('add-referrer-address');
    if (referrerValue !== defaultReferrer) {
        disableButton(addReferrerBtn);
        disableButton(addReferrerInput);
    }
}

async function getTotalBnbAMount() {
    const functionName = 'totalBNBAmount';
    const ABI_totalBnbAMount = [{ "inputs": [], "name": "totalBNBAmount", "outputs": [{ "internalType": "uint256", "name": "", "type": "uint256" }], "stateMutability": "view", "type": "function" }];
    let raisedAmount = await readFunction(functionName, ABI_totalBnbAMount);
    raisedAmount = raisedAmount / Math.pow(10, 15);
    return raisedAmount;
}

async function getReferrer(userAddress) {
    const ABI_getReferrer = [{ "inputs": [{ "internalType": "address", "name": "_user", "type": "address" }], "name": "getReferrer", "outputs": [{ "internalType": "address", "name": "", "type": "address" }], "stateMutability": "view", "type": "function" }];

    let options = {
        contractAddress: REFER_CONTRACT_ADDRESS_TESTNET,
        functionName: 'getReferrer',
        abi: ABI_getReferrer,
        params: {
            _user: userAddress,
        }
    }

    return await Moralis.executeFunction(options);
}

async function populateUserReferralsCount(userAddress) {
    let countValue = userAddress; //which will be 'logged out' if user is logged out
    // if user is logged-in we change the value of refferValue
    if (isUserConnected()) countValue = await getReferralsCount(userAddress);
    const referrerFields = document.querySelectorAll('.refer-total');
    referrerFields && referrerFields.forEach(el => {
        // in this example - the countValue = 0
        el.innerHTML = countValue;
    });
}

