async function addReferrer() {
    const referrerAddress = document.querySelector('#add-referrer-address');
    const errContainer = document.querySelector('#buy-token-error-con');
    const errMsgContainer = document.querySelector('#buy-token-error-msg');
    // const errContainer = document.querySelector('#add-referrer-err');
    // const errMsgContainer = document.querySelector('#add-referrer-msg');
    // let refAdd = referrerAddress.value + '';
    // refAdd = refAdd.toLowerCase();



    // console.log(referrerAddress);
    // console.log(referrerAddress.value);

    /**
     * if the existing referrer field is populated and is equal to default referrer then show error.
     */
    if (referrerAddress && !referrerAddress.value) {
        showErrMsg(errContainer, errMsgContainer, 'Paste Your Referral Wallet Address below');
        // showErrMsg(errContainer, errMsgContainer, 'Empty Referrer Address Field!');
        return;
    } else if (referrerAddress && referrerAddress.value.length <= 6) {
        showErrMsg(errContainer, errMsgContainer, 'Invalid Address!');
        return;
    } else if (isUserConnected) {
        // if (!Moralis.isWeb3Enabled()) await Moralis.enableWeb3();
        // Moralis.enableWeb3(); // blah
        if (!isUserConnected()) {
            showErrMsg(errContainer, errMsgContainer, 'Wallet not connected!');
            return;
        }
    } else {
        console.error(`referrerAddress#add-referrer-address missing`);
        // return;
    }

    const existingReferrerValue = document.querySelector('.refer-referrer').value;
    console.log(existingReferrerValue);
    console.log(existingReferrerValue !== defaultReferrer);
    console.log(defaultReferrer);
    if (existingReferrerValue !== defaultReferrer) {
        console.log(`======= ${existingReferrerValue}`);
        showErrMsg(errContainer, errMsgContainer, 'Referrer Address already setup!');
        return;
    }
    // at this point the refer address exists, isn't empty and length is > 6 characters

    try {
        let result = await setReferAddress(referrerAddress.value);
        // let result = await setReferAddress(refAdd);
        populateReferrerinHTML(getUserWalletAddress());
        console.log(result);

    } catch (error) {
        console.error(error);
        showErrMsg(errContainer, errMsgContainer, 'Invalid Address!!!');
    }
}
async function setReferAddress(referrerAddress) {

    const ABI_totalReferralCommissions = [{ "inputs": [{ "internalType": "address", "name": "referAddress", "type": "address" }], "name": "addReferAddress", "outputs": [], "stateMutability": "nonpayable", "type": "function" }];
    console.log(`setReferAddress to - ${referrerAddress}`);
    let options = {
        contractAddress: REFER_CONTRACT_ADDRESS_TESTNET,
        functionName: 'addReferAddress',
        abi: ABI_totalReferralCommissions,
        params: {
            referAddress: referrerAddress,
        }
    }

    let transaction;
    try {
        transaction = await Moralis.executeFunction(options);
    } catch (error) {
        console.error(error);
    }

    return await transaction.wait();
}

async function populateUserReferralComissions(userAddress) {
    console.log(`Getting commission of: ${userAddress}`);
    let commissionValue = userAddress; //which will be 'logged out' if user is logged out
    // if user is logged-in we change the value of refferValue
    if (isUserConnected()) commissionValue = await getTotalReferralCommissions(userAddress);
    // commissionValue = commissionValue / Math.pow(10, DECIMALS);
    commissionValue = commissionValue / Math.pow(10, 9);

    const commssionFields = document.querySelectorAll('.refer-earning');
    commssionFields && commssionFields.forEach(el => {
        console.log(`Setting commission to: ${commissionValue}`);
        // in this example - the countValue = 0
        el.innerHTML = `${commissionValue} IBAT`;
    });
}

