async function populateUsersInvestments(userAddress) {
    console.log(`Getting commission of: ${userAddress}`);
    let investmentValue = userAddress; //which will be 'logged out' if user is logged out
    // if user is logged-in we change the value of refferValue
    if (isUserConnected()) investmentValue = await getUsersInvestments(userAddress);
    investmentValue = investmentValue / Math.pow(10, DECIMALS);

    const investmentFields = document.querySelectorAll('.user-investment');
    investmentFields && investmentFields.forEach(el => {
        console.log(`Setting investment to: ${investmentValue}`);
        // in this example - the countValue = 0
        el.innerHTML = `${investmentValue} IBAT`;
    });
}

