function copyReferLink() {
    // get the referralLink
    /**
        if user logged in - show the box 
        else nothing OR hide the box at logout
    * 
    */
    let userAddress = getUserWalletAddress();
    // store it in variable
    const URL_Prefix = 'https://presale.battleinfinity.io/?refercode='
    let USER_RefferalLink = `${URL_Prefix}${userAddress}`;
    populateReferralLinkinHTML(USER_RefferalLink);

    navigator.clipboard.writeText(USER_RefferalLink);

    // console.log(`copied`);
    // populateReferralLinkinHTML('bkahsasdasd');
}
function populateReferralLinkinHTML(referralLink) {
    const linkNodes = document.querySelectorAll('.refer-link-text');
    linkNodes && linkNodes.forEach(el => {
        el.innerHTML = referralLink;
        el.value = referralLink;
    });
}
